import React, { Component } from 'react';
import Navbar from './navbar';
import ArticleManage from './ArticleManage';
import TagManage from './TagManage';
import CategoryManage from './CategoryManage'
import AsideMenu from './AsideMenu'
import $ from 'jquery'

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      page: 'blogManage'
    }
    this.contentPage = {
      blogManage: <ArticleManage />,
      tagManage: <TagManage />,
      categoryManage: <CategoryManage />,
    }
  }

  componentDidMount() {
    let token = $("meta[name='_csrf']").attr("content");
    let header = $("meta[name='_csrf_header']").attr("content");
    $.ajaxSetup({
      beforeSend: function (xhr) {
        if (header && token) {
          xhr.setRequestHeader(header, token);
        }
      }
    })

  }

  onPageChange(newPage) {
    this.setState({
      page: newPage
    })
  }

  render() {
    return (
      <div className="h-100">
        <Navbar />
        <div className="container-fluid h-100">
          <div className="row h-100">
            <div className="col-2 h-100 p-0 m-0 aside">
              <AsideMenu onPageChange={(page) => this.onPageChange(page)} page={this.state.page} />
            </div>
            <div className="col-10 h-100 m-0 pt-2 px-5" >
              {this.contentPage[this.state.page]}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



