import React, { Component } from 'react';
import Modal from './Modal';
import $ from 'jquery'

export default class CategoryManage extends Component {
    constructor(props) {
        super(props)
        this.url = 'category'
        this.state = {
            categories: [],
            modalData: { 
                title: '',
                description: '',
            },
        }
        this.modal = React.createRef()
        this.form = {
            nameInput: React.createRef(),
            descriptionInput: React.createRef(),
        }
        this.formData = {
            id: null,
            name: null,
            description: null,
        }
    }

    componentDidMount() {
        this.updateData()
    }

    updateData() {
        console.log('loading categories')
        $.get({
            url: this.url,
            success: (data) => {
                console.log(data)
                if (data.status === 'succeed') {
                    this.setState({
                        categories: data.data
                    })
                }
            },
            dataType: 'json'
        }).fail(function() {
            alert('fial loading categories')
        })
    }

    onAddCategory() {
        console.log('add category');
        this.formData.id = null
        this.setState({
            modalData: {
                title:'',
                description: '',
            },
        })
        this.modal.current.show()
    }

    onModifyCategory(c) {
        this.formData.id = c.id
        this.form.nameInput.current.value = c.name
        this.setState({
            modalData: {
                title: '' + c.id,
                description: c.description,
            },
        })
        this.modal.current.show()
    }

    saveFormData() {
        this.formData = {
            id: this.formData.id,
            name: this.form.nameInput.current.value,
            description: this.form.descriptionInput.current.value,
        }
    }

    submitForm() {
        this.saveFormData()
        let method = this.formData.id? 'PUT' : 'POST'
        $.ajax({
            method: method,
            url: this.url,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })
    }

    onDeleteCategory(c) {
        if (c.articles && c.articles.length !== 0) {
            alert('!')
            return
        }
        $.ajax({
            method: 'DELETE',
            url: this.url + '/' + c.id,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })
    }

    tableRows() {
        return this.state.categories.map((c, i) => {
            return (
                <tr key={i}>
                    <th>{c.id}</th>
                    <td>{c.name}</td>
                    <td>
                        <a onClick={() => this.onModifyCategory(c)} className="btn btn-primary mr-2"></a>
                        <a onClick={() => this.onDeleteCategory(c)} className="btn btn-danger"></a>
                    </td>
                </tr >
            )
        })
    }

    render() {
        return (
            <div>
                <div className="card">
                    <div className="card-header">
                        
                    <button onClick={() => this.onAddCategory()} className="btn btn-primary float-right"></button>
                    </div>
                    <table className="table table-hover table-striped">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.tableRows()}
                        </tbody >
                    </table>
                </div >

                <Modal ref={this.modal}>
                    <div className="modal-header">
                        <h5 className="modal-title" id="modal-title">{this.state.modalData.title}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <form id="tag-form">
                            <div className="form-group">
                                <label htmlFor="tag-name" className="col-form-label"></label>
                                <input ref={this.form.nameInput} defaultValue={this.state.modalData.name} type="text" className="form-control"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="tag-description" className="col-form-label"></label>
                                <textarea ref={this.form.descriptionInput} className="form-control" name="description">{this.state.modalData.description}</textarea>
                            </div>
                        </form>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                        <button onClick={()=> this.submitForm()} type="button" className="btn btn-primary" data-dismiss="modal"></button>
                    </div>
                </Modal>
            </div>
        )
    }
}




