import React, { Component } from 'react';
import Modal from './Modal'
import $ from 'jquery'
import editormd from 'editormd'

export default class ArticleManage extends Component {
    constructor(props) {
        super(props)
        this.url = 'article'
        this.state = {
            articles: [],
            modalData: {
                title: '',
                articleTitle: '',
                summary: '',
                mdContent: '',
                imgsrc: '',
            }
        }
        this.modal = React.createRef()
        this.form = {
            titleInput: React.createRef(),
            categoryNameInput: React.createRef(),
            tagNameInput: React.createRef(),
            imgFileInput: React.createRef(),
            summaryInput: React.createRef(),
            editormd: null,
        }
        this.editor = null
        this.formData = {
            id: null,
            title: '',
            categoryName: '',
            tagNames: '',
            articleImgBase64: null,
            summary: '',
            mdContent: '',
            htmlContent: '',
        }
    }

    componentDidMount() {
        this.form.editormd = editormd("editormd", {
            path: "/vendor/editor/lib/",
            width: "100%",
            height: 800,
            saveHTMLToTextarea: true,
        })
        this.updateData()
    }

    updateData() {
        console.log('loading articles')
        $.get({
            url: this.url,
            success: (data) => {
                console.log(data)
                if (data.status === 'succeed') {
                    this.setState({
                        articles: data.data
                    })
                }
            },
            dataType: 'json'
        }).fail(function() {
            alert('fial loading articles')
        })
    }

    onAddArticle() {
        this.formData.id = null
        this.setState({
            modalData: {
                title: '',
            }
        })
        this.modal.current.show()
    }

    onModifyArticle(a) {
        this.formData.id = a.id
        this.setState({
            modalData: {
                title: '',
                imgsrc: a.imgLink,
                mdContent: a.mdContent,
            }
        })
        this.form.titleInput.current.value = a.title
        this.form.imgFileInput.current.value = ''
        this.form.summaryInput.current.value = a.summary
        this.modal.current.show()
    }

    saveFormData() {
        let form = this.form
        this.formData = {
            id: this.formData.id,   
            title: form.titleInput.current.value,
            categoryName: form.categoryNameInput.current.value,
            tagNames: form.tagNameInput.current.value,
            articleImgBase64: this.state.modalData.imgsrc,               
            summary: form.summaryInput.current.value,
            mdContent: form.editormd.getMarkdown(),
            htmlContent: form.editormd.getHTML(),
        }
    }

    submitForm() {
        this.saveFormData()
        let method = this.formData.id? 'PUT' : 'POST'
        $.ajax({
            method: method,
            url: this.url,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })
    }

    deleteArticle(a) {
        $.ajax({
            method: 'DELETE',
            url: this.url,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })
    }

    readerImg() {
        let file = this.form.imgFileInput.current.files[0]
        let reader = new FileReader()
        reader.onload = e => {
            this.setState({
                modalData: {
                    imgsrc: reader.result
                }
            })
        }
        reader.readAsDataURL(file)
    }

    tableRows() {
        return this.state.articles.map((a, i) => {
            return (
                <tr key={i}>
                    <th>{a.id}</th>
                    <td>{a.title}</td>
                    <td>
                        <a onClick={() => this.onModifyArticle(a)} className="btn btn-primary mr-2"></a>
                        <a onClick={() => this.deleteArticle(a)} className="btn btn-danger"></a>
                    </td >
                </tr >
            )
        })
    }

    render() {
        return (
            <div>
                <div className="card">
                    <div className="card-header">
                        
                    <button onClick={() => this.onAddArticle()} className="btn btn-primary float-right"></button>
                    </div>
                    <table className="table table-hover table-striped">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.tableRows()}
                        </tbody >
                    </table>
                </div >


                <Modal ref={this.modal}>
                    <div className="modal-header">
                        <h5 className="modal-title" id="modal-title">{this.state.modalData.title}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <form enctype="multipart/form-data" method="POST" id="blog-form">
                            <fieldset className="form-group">
                                <label htmlFor="input-title"></label>
                                <input ref={this.form.titleInput} type="text" className="form-control" name="title" id="input-title" />
                            </fieldset>
                            <fieldset className="form-group">
                                <label htmlFor="input-category"></label>
                                <input ref={this.form.categoryNameInput} type="text" className="form-control" name="categoryName" id="input-category" />
                            </fieldset>
                            <fieldset className="form-group">
                                <label htmlfor="input-tags"></label>
                                <input ref={this.form.tagNameInput} type="text" className="form-control" name="tagNames" id="input-tags" />
                                <small className="text-muxd">;</small>
                            </fieldset>
                            <fieldset className="form-group">
                                <label htmlFor="article-img"></label>
                                <input ref={this.form.imgFileInput} onChange={() => this.readerImg()} type="file" className="form-control-file"  name="articleImg" />
                                <img src={this.state.modalData.imgsrc} alt="" />
                            </fieldset>
                            <fieldset className="form-group">
                                <label htmlFor="summary"></label>
                                <input ref={this.form.summaryInput} type="text" className="form-control" name="summary" id="summary" />
                            </fieldset>
                        </form>
                        <div class="form-group">
                            <div class="editormd" id="editormd">
                                <textarea style={{ display: 'none' }}>{this.state.modalData.mdContent}</textarea>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                        <button onClick={() => this.submitForm()} type="button" className="btn btn-primary" data-dismiss="modal"></button>
                    </div>
                </Modal>

            </div>
        )
    }
}


