import React, { Component } from 'react';
import Modal from './Modal'
import $ from 'jquery'

export default class TagManage extends Component {
    constructor(props) {
        super(props)
        this.url = 'tag'
        this.state = {
            tags: [],
            modalData: {
                title: '',
                description: '',
            },
        }
        this.modal = React.createRef()
        this.form = {
            nameInput: React.createRef(),
            descriptionInput: React.createRef(),
        }
        this.formData = {
            id: null,
            name: null,
            description: null,
        }
    }

    componentDidMount() {
        this.updateData()
    }

    updateData() {
        console.log('loading tags')
        $.get({
            url: this.url,
            success: (data) => {
                console.log(data)
                if (data.status === 'succeed') {
                    this.setState({
                        tags: data.data
                    })
                }
            },
            dataType: 'json'
        }).fail(function() {
            alert('fial loading tags')
        })
    }


    onAddTag() {
        this.formData.id = null
        this.form.nameInput.current.value = ''
        this.setState({
            modalData: {
                title:'',
                description: '',
            },
        })
        this.modal.current.show()
    }

    onModifyTag(t) {
        this.formData.id = t.id
        this.form.nameInput.current.value = t.name
        this.setState({
            modalData: {
                title: '' + t.id,
                description: t.description,
            },
        })
        this.modal.current.show()
    }
    
    saveFormData() {
        this.formData = {
            id: this.formData.id,
            name: this.form.nameInput.current.value,
            description: this.form.descriptionInput.current.value,
        }
    }

    submitForm() {
        this.saveFormData()
        let method = this.formData.id? 'PUT' : 'POST'
        $.ajax({
            method: method,
            url: this.url,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })
    }

    onDeleteTag(t) {
        if (t.articles && t.articles.length !== 0) {
            alert('')
            return
        }
        $.ajax({
            method: 'DELETE',
            url: this.url + '/' + t.id,
            data: this.formData,
            dataType: 'json',
            success: (data) => {
                console.log(data)
                if (data.status !== 'succeed') {
                    console.log(data.error)
                }
                this.updateData()
            }
        })

    }

    tableRows() {
        return this.state.tags.map((t, i) => {
            return (
                <tr key={i}>
                    <th>{t.id}</th>
                    <td>{t.name}</td>
                    <td>
                        <a onClick={() => this.onModifyTag(t)} className="btn btn-primary mr-2"></a>
                        <a onClick={() => this.onDeleteTag(t)} className="btn btn-danger"></a>
                    </td>
                </tr >
            )
        })
    }

    render() {
        return (
            <div>
                <div className="card">
                    <div className="card-header">
                        
                    <button onClick={() => this.onAddTag()} className="btn btn-primary float-right"></button>
                    </div>
                    <table className="table table-hover table-striped">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.tableRows()}
                        </tbody >
                    </table>
                </div >

                <Modal ref={this.modal}>
                    <div className="modal-header">
                        <h5 className="modal-title" id="modal-title">{this.state.modalData.title}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <form id="tag-form">
                            <div className="form-group">
                                <label htmlFor="tag-name" className="col-form-label"></label>
                                <input ref={this.form.nameInput} defaultValue={this.state.modalData.name} type="text" className="form-control" name="name" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="tag-description" className="col-form-label"></label>
                                <textarea ref={this.form.descriptionInput} className="form-control" name="description">{this.state.modalData.description}</textarea>
                            </div>
                        </form>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                        <button onClick={() => this.submitForm()} type="button" className="btn btn-primary" data-dismiss="modal"></button>
                    </div>
                </Modal>
            </div>
        )
    }
}



