import React from 'react';
import './AsideMenu.css'

export default function AsideMenu(props) {
    return (
        <ul className="nav flex-column">
            <li onClick={() => props.onPageChange('blogManage')} className={'nav-item ' + (props.page === 'blogManage'? 'active' : '')} >
                <button className="btn btn-flat">
                
                </button>
            </li>
            <li  onClick={() => props.onPageChange('tagManage')} className={'nav-item ' + (props.page === 'tagManage'? 'active' : '')} >
                <button className="btn btn-flat">
                
                </button>
            </li >
            <li onClick={() => props.onPageChange('categoryManage')} className={'nav-item ' + (props.page === 'categoryManage'? 'active' : '')} >
                <button className="btn btn-flat">
                
                </button>
            </li >
        </ul >
    )
}


